  /*form design*/
  .flex-row {
    display:flex;
    flex-direction: row;
  }

  .flex-col {
    display: flex;
    flex-direction: column;
  }

  .form-group {
    padding: 30px;
    border-bottom: 1px solid #eee;
  }
  
  label {
    font-size: 16px;
    color: #222;
  }

