  input[type="number"] {
    border: 1px solid #ddd;
    border-radius: 2px;
    font-size: 16px;
    padding: 0 8px;
    height: 30px;
    font-weight: 500;
  }

  input[type="number"]:focus {
    box-shadow: 0 0 4px #528fcc;
    border-color: rgba(0,0,0,0);
    background: #fff;
  }

  input:not([type]), input[type="text"], input[type="password"], input[type="date"], input[type="datetime"], input[type="datetime-local"], input[type="month"], input[type="week"], input[type="email"], input[type="search"], input[type="tel"], input[type="time"], input[type="url"], input[type="color"] {
    -webkit-appearance: none;
    -moz-appearance: none;
    border-width: 0;
    background-color: #FFFFFF;
